import React, { useEffect, useState } from 'react';
import Layout from '../components/layout';
import { graphql } from 'gatsby';
import styled from 'styled-components';
import StaticAds from '../components/static-ads'

export const breakpoints = {
  sm: 20,
  md: 30,
  lg: 45,
  xl: 60
};

export const mediaQueries = (key) => {
  return (style) =>
    `@media (min-width: ${breakpoints[key]}em) { ${style} }`;
};

const OnecompilerTemplate = ({ data, location }) => {
  const [isDisplay, setIsDisplay] = useState(true);
  const [iframeHtml, setIframeHtml] = useState("");
  const { html, frontmatter } = data.markdownRemark;

  useEffect(() => {
    setInterval(() => {
      setIsDisplay(false)
      setIsDisplay(true)
    }, 30000)
  }, []);

  const params = new Proxy(new URLSearchParams(location.search), {
    get: (searchParams, prop) => searchParams.get(prop),
  });

  useEffect(() => {
    if (params.language && params.code) {
      let iframeHtml = `<iframe
          frameBorder="0"
          height="500px"
          src="https://onecompiler.com/embed/${params.language}/${params.code}?hideNew=true&hideNewFileOption=true&hideTitle=true&hideStdin=true"
          width="100%"
        ></iframe>`;
      setIframeHtml(iframeHtml);
    } else {
      let iframeHtml = `<iframe
      frameBorder="0"
      height="500px"
      src="https://onecompiler.com/embed/javascript?hideNew=true&hideNewFileOption=true&hideTitle=true&hideStdin=true"
      width="100%"
    ></iframe>`;
      setIframeHtml(iframeHtml);
    }
  }, [params.language, params.code]);

  return (
    <Layout title={frontmatter.title}>
      <link rel="canonical" href="https://aguidehub.com/onecompiler/"></link>
      <PostWrapper>
        <PostContent dangerouslySetInnerHTML={{ __html: html }} />
        <EditorWrapper>
          <LeftWrapper>
            <PostContent dangerouslySetInnerHTML={{ __html: iframeHtml }} />
          </LeftWrapper>
          <RightWrapper>
            {isDisplay && <StaticAds />}
          </RightWrapper>
        </EditorWrapper>
      </PostWrapper>
    </Layout>
  );
};

export default OnecompilerTemplate;

const EditorWrapper = styled.div`
  flex-direction: row;
  display: flex;
`;

const LeftWrapper = styled.div`
  flex: 4;
  height: 600px;
`;

const RightWrapper = styled.div`
  display: none;
  ${mediaQueries("md")`
    margin: 10px;
    display: flex;
    flex-direction: column;
    flex: 1;
  `};
`;

const PostWrapper = styled.div`
  padding-top: var(--size-900);
  padding-bottom: var(--size-900);
  margin-left: auto;
  flex-direction: column;
  display: flex;
  margin-right: auto;
  word-wrap: break-word;
`;

const PostContent = styled.section`
  padding-top: var(--size-800);

  & > * + * {
    margin-top: var(--size-300);
  }

  & > p + p {
    margin-top: var(--size-700);
  }

  * + h1,
  * + h2,
  * + h3 {
    margin-top: var(--size-900);
  }

  h1 {
    font-size: var(--size-800);
  }

  h2 {
    font-size: var(--size-700);
  }

  h3 {
    font-size: var(--size-700);
  }

  b,
  strong {
    font-weight: 600;
  }

  a {
    color: inherit;
    text-decoration: underline;
    text-decoration-thickness: 0.125rem;
  }

  blockquote {
    padding-left: var(--size-400);
    border-left: 5px solid;
    font-style: italic;
  }

  code {
    font-family: 'Source Sans Pro', monospace;
    overflow-x: auto;
    white-space: pre-wrap;
  }

  pre {
    overflow-x: auto;
    white-space: pre-wrap;
    max-width: 100%;
  }
`;

export const pageQuery = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        profile_image
      }
    }
  }
`;
